* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.profile {
  margin: 0 auto;  
  margin-top: 20px;
  width: 300px;  
  display: flex;
  flex-direction: column;  
  align-items: center;
  background-color: #E7ECF2;
  padding: 1rem;
  -webkit-box-shadow: -1px 10px 5px 0px rgba(194,194,194,1);
-moz-box-shadow: -1px 10px 5px 0px rgba(194,194,194,1);
box-shadow: -1px 10px 5px 0px rgba(194,194,194,1);
}

.description {
  background-color: #FFFFFF;
  margin: 0 auto;
  width: 80%;
}

.stats {  
  background-color: #F3F6F9;
  display: flex;
  width: 80%;
  justify-content: space-around;
  -webkit-box-shadow: -1px 10px 5px 0px rgba(194,194,194,1);
-moz-box-shadow: -1px 10px 5px 0px rgba(194,194,194,1);
box-shadow: -1px 10px 5px 0px rgba(194,194,194,1);  
}

.statchildren {
  width: 50%;
  display: flex;
  flex-direction: column;
  border: 1px solid rgb(202, 198, 198);
  padding: 10px;
}

.avatar {
  border-radius: 50%;
  display: block;
  margin: 0 auto;
  padding: 1rem;
}

.name,.tag,.location{
  text-align: center;
  font-family: "Times New Roman", Times, serif;
  padding: 3px;
}

.tag,.location{
  color: #768696;
  font-size: 16px;
}
.name {
  color: #4B525E;
  font-size: 24px;
}

.location {
  padding-bottom: 10px;
}



.label,.quantity{
  text-align: center;
  font-family: "Times New Roman", Times, serif;
}

.label {
  font-size: 16px;
}

.quantity {
  font-size: 14px;
  font-weight: 900;
  margin-top: 8px;
}